import React, { useState, useContext, useEffect } from 'react';
import { useNavigate } from "react-router-dom";
import axios from 'axios';
import AnimateError from '../validation/AnimateError';

import { UserContext } from "../../App";

export default function AdminLogin() {
    const {state, dispatch} = useContext(UserContext);
    const {adm, dispadm} = useContext(UserContext);
    let navigate = useNavigate();
    const [disable, setDisable] = useState(false);
    const [srvErr, setSrvErr] = useState([]);
    const [password, setPassword] = useState('');
    const [username, setUsername] = useState('');
    const [errors, setErrors] = useState({
        password: '',
        username: ''
    });
    const logSubmit = (e) => {
        e.preventDefault();
        let errorCount = 0;
        if(username === '') {
            errorCount++;
            setErrors((prevState)=> {
                return {...prevState, username: 'Username is Required'}
            });
            AnimateError();
        } else {
            setErrors((prevState)=> {
                return {...prevState, username: ''}
            });
        } if(password === '') {
            errorCount++;
            setErrors((prevState)=> {
                return {...prevState, password: 'Password is Required'}
            });
            AnimateError();
        } else {
            setErrors((prevState)=> {
                return {...prevState, password: ''}
            });
        }
        if(errorCount === 0) {
            dispatch({type:"USER", payload:false});
            localStorage.removeItem("token");
            setSrvErr("");
            setDisable(true);
            let logData = {username, password}
            axios.post('api/adminlogin', logData)
            .then(function (res) {
                localStorage.setItem("admin", "Bearer " + res.data.adm);
                dispadm({type:"USER", payload:true});
                navigate("/admindashboard");
                setDisable(false);
            })
            .catch(function (error) {
                setSrvErr(error.response.data);
                setDisable(false);
            });
        }
    }
    useEffect(() => {
        if(adm) {
            navigate("/");
        }
        localStorage.removeItem("token");
        return () => {
            dispatch({type:"USER", payload:false});
        };
    }, []);
    return (
        <>
            <form className="auth_form contact-form" autoComplete="off" onSubmit={logSubmit}>
                <div className="row">
                    <div className="col-sm-12">
                        <div className="form_wr_input">
                            <input className="form_input_style" onChange={(e)=>setUsername(e.target.value)} type="text" placeholder="Username" />
                            <span className="form_err">{errors.username}</span>
                        </div>
                    </div>
                    <div className="col-sm-12">
                        <div className="form_wr_input">
                            <input className="form_input_style" onChange={(e)=>setPassword(e.target.value)} type="password" placeholder="Password" />
                            <span className="form_err">{errors.password}</span>
                        </div>
                        <button className="site-btn sb-big" disabled={disable}>Login</button>
                    </div>
                    <div className="col-sm-12 server_error">
                        {srvErr && srvErr.map(err =>
                            <div key={err.msg} className="form_err">{err.msg}</div>
                        )}
                    </div>
                </div>
            </form>
        </>
    )
}
